.comments-grid {
	grid-column-start: 2;
	padding-left: 15px;
	.wrapper {
		display: flex;
		flex-direction: column;
		.thread {
			padding-left: 50px; }
		.delited-comment {
			height: 47px;
			color: #666666;
			padding-left: 50px;
			padding-top: 10px;
			display: flex; }
		.comment-card {
			display: flex;
			flex-direction: row;
			padding-bottom: 15px;
			img {
				border-radius: 50%;
				width: 40px;
				height: 40px; }
			.attached-img {
				border-radius: 4px;
				width: auto;
				height: auto; }
			.comment {
				display: flex;
				flex-direction: column;
				padding-left: 10px;
				a {
					text-decoration: none;
					color: #2a5885;
					font-weight: 500; }
				.comment-like {
					display: flex;
					flex-direction: row;
					align-items: center;
					font-weight: 500;
					color: #67707A;
					.like {
						background: url(../svg/like_16.svg) no-repeat;
						width: 16px;
						height: 16px;
						padding-right: 4px; } } } } } }

.backdrop {
	position: fixed;
	top: 0;
	left: 0;
	background-color: rgba(0,0,0,0.3);
	height: 100vh;
	width: 100%;
	.modal {
		position: relative;
		display: flex;
		height: 100vh;
		align-items: center;
		justify-content: center; } }
