@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic-ext);
.profile-card {
  display: flex; }
  .profile-card img {
    width: 28px;
    height: 28px;
    border-radius: 50%;
    margin: 7px 0; }
  .profile-card p {
    font-size: 12.5px;
    font-weight: 500;
    padding-right: 10px;
    display: inline-block;
    line-height: 42px;
    color: #2a5885; }

.groups-h1 {
  font-size: 38px;
  padding-left: 15px;
  padding-bottom: 40px; }

.groups-grid {
  justify-content: center;
  grid-column-start: 2;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  padding: 0 15px; }

.group-card {
  display: flex;
  padding: 10px 0;
  border-bottom: 1px solid #e7e8ec; }
  .group-card img {
    border-radius: 50%;
    height: 80px; }
  .group-card .info {
    padding-top: 6px;
    margin-left: 15px;
    flex-direction: column;
    align-items: center;
    color: #656565; }
  .group-card .group-name {
    font-weight: 700;
    color: #2a5885;
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
    -webkit-text-decoration-style: solid;
            text-decoration-style: solid;
    -webkit-text-decoration-style: initial;
            text-decoration-style: initial;
    -webkit-text-decoration-color: inherited;
            text-decoration-color: inherited;
    -webkit-text-decoration-color: initial;
            text-decoration-color: initial; }

.wall-grid {
  grid-column-start: 2;
  display: grid;
  grid-column-gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  grid-auto-rows: 5px; }

.item {
  text-decoration: none;
  width: 100%; }
  .item .wall-post {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    background-color: #ffffff;
    border-radius: 4px;
    box-shadow: 0 1px 0 0 #d7d8db, 0 0 0 1px #e3e4e8;
    width: 100%;
    padding: 15px;
    color: #000000; }
    .item .wall-post .post-date {
      padding-bottom: 9px; }
    .item .wall-post .post-text {
      max-height: 155px;
      overflow: hidden; }
    .item .wall-post .post-img-div {
      width: 100%;
      padding-top: 15px;
      line-height: 0; }
      .item .wall-post .post-img-div .attached-img {
        width: 100%; }
    .item .wall-post .post-info {
      display: flex;
      flex-direction: row;
      padding-top: 15px; }
      .item .wall-post .post-info div {
        align-items: center;
        display: flex;
        flex-direction: row;
        font-size: 14px;
        color: #67707a;
        font-weight: 500; }
      .item .wall-post .post-info .views {
        margin-left: auto; }
      .item .wall-post .post-info .like-svg {
        background: url(/eastkorzh/test-redux/static/media/like_outline_24.37bcdecd.svg) no-repeat;
        width: 24px;
        height: 24px;
        margin-right: 5px; }
      .item .wall-post .post-info .reposts-svg {
        background: url(/eastkorzh/test-redux/static/media/share_outline_24.06ac0036.svg) no-repeat;
        width: 24px;
        height: 24px;
        margin-right: 5px;
        margin-left: 15px; }
      .item .wall-post .post-info .comments-svg {
        background: url(/eastkorzh/test-redux/static/media/comment_outline_24.e69d0c97.svg) no-repeat;
        width: 24px;
        height: 24px;
        margin-right: 5px;
        margin-left: 15px; }
      .item .wall-post .post-info .view-svg {
        background: url(/eastkorzh/test-redux/static/media/view_24.7d449b62.svg) no-repeat;
        width: 24px;
        height: 24px;
        margin-right: 5px; }

.back {
  grid-column-start: 2;
  display: flex;
  align-items: center;
  padding-bottom: 40px; }
  .back .back-svg {
    background: url(/eastkorzh/test-redux/static/media/back_24.d88ec05c.svg) no-repeat;
    height: 24px;
    width: 24px; }
  .back .back-img img {
    border-radius: 50%;
    margin-left: 15px; }
  .back .back-h1 {
    font-size: 1.2rem;
    margin-left: 10px; }

.comments-grid {
  grid-column-start: 2;
  padding-left: 15px; }
  .comments-grid .wrapper {
    display: flex;
    flex-direction: column; }
    .comments-grid .wrapper .thread {
      padding-left: 50px; }
    .comments-grid .wrapper .delited-comment {
      height: 47px;
      color: #666666;
      padding-left: 50px;
      padding-top: 10px;
      display: flex; }
    .comments-grid .wrapper .comment-card {
      display: flex;
      flex-direction: row;
      padding-bottom: 15px; }
      .comments-grid .wrapper .comment-card img {
        border-radius: 50%;
        width: 40px;
        height: 40px; }
      .comments-grid .wrapper .comment-card .attached-img {
        border-radius: 4px;
        width: auto;
        height: auto; }
      .comments-grid .wrapper .comment-card .comment {
        display: flex;
        flex-direction: column;
        padding-left: 10px; }
        .comments-grid .wrapper .comment-card .comment a {
          text-decoration: none;
          color: #2a5885;
          font-weight: 500; }
        .comments-grid .wrapper .comment-card .comment .comment-like {
          display: flex;
          flex-direction: row;
          align-items: center;
          font-weight: 500;
          color: #67707A; }
          .comments-grid .wrapper .comment-card .comment .comment-like .like {
            background: url(/eastkorzh/test-redux/static/media/like_16.d6f67df9.svg) no-repeat;
            width: 16px;
            height: 16px;
            padding-right: 4px; }

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3);
  height: 100vh;
  width: 100%; }
  .backdrop .modal {
    position: relative;
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center; }

.sort-button {
  margin-right: 7px;
  margin-top: 19px;
  outline: none;
  border: 1px solid #5181b8;
  border-radius: 4px;
  height: 28px;
  width: 172px;
  background-color: transparent;
  color: #5181b8;
  font-weight: 600;
  font-size: 14px; }

* {
  padding: 0;
  margin: 0; }

body {
  margin: 0;
  background: #edeef0;
  font-family: 'Roboto', sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.462;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: auto; }

.logged-user {
  grid-column-start: 2;
  display: flex;
  justify-content: flex-end; }
  .logged-user .login-button {
    border-radius: 3px;
    outline: none;
    background-color: #5181b8;
    border: none;
    color: #fff;
    width: 80px;
    height: 28px;
    margin: 7px 10px;
    font-size: 12.5px; }
    .logged-user .login-button:hover {
      background-color: #5b88bd; }
    .logged-user .login-button:active {
      background-color: #4872a3;
      padding-top: 8px;
      padding-bottom: 7px; }

.app-grid {
  display: grid;
  grid-template-columns: 1fr minmax(250px, 1300px) 1fr; }
  .app-grid h1 {
    grid-column-start: 2; }
  .app-grid .top-pannel {
    grid-column-start: 2;
    display: flex;
    justify-content: space-between; }
    @media (max-width: 550px) {
      .app-grid .top-pannel {
        flex-direction: column; }
        .app-grid .top-pannel .sort-button {
          margin-left: 15px;
          margin-bottom: 15px; }
        .app-grid .top-pannel .back {
          padding-bottom: 0; } }

